<template>
  <div id="TheSideMove">
    <section>
      <router-view></router-view>
    </section>
    <section>
      <VIcon 
      @click.native="isShowSide()"
      icon="keyboard_arrow_right" 
      iclass="small pink lighten-1 white-text pulse">
      </VIcon>
    </section>
    <transition
      enter-active-class="showAndHide-enter-active"
      leave-active-class="showAndHide-leave-active"
    >
    <section v-if="showSide">
      <VSideNavbar @emit="isShowSide"></VSideNavbar>
      <div @click="isShowSide()">
        <VIcon 
        iclass="medium pink-text lighten-1">
        </VIcon>
      </div>
    </section>
    </transition>
  </div>
</template>
<script>
const VIcon = () => import("@/components/other/VIcon.vue")
const VSideNavbar = () => import("@/components/project/VSideNavbar.vue")


export default {
  name: 'TheSideMove',
  data() {
    return {
      showSide: false
    }
  },
  components: {
    VIcon,
    VSideNavbar
  },
  methods:{
  	isShowSide(){
  		this.showSide = !this.showSide
  	}
  }
};
</script>
<style lang="scss" scoped>
@import "@/assets/style/style.scss";

#TheSideMove {
  section:nth-of-type(1) {
    width: 100%;

    div {
      margin: auto 15%;
    }
  }

  section:nth-of-type(2) {
    .VIcon {
      position:fixed;
      top:20px;
    }
  }

  section:nth-of-type(3) {
    z-index:99;
    position: fixed;
    display: flex;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;


    div:nth-of-type(1) {
      width: 350px;
      background-color: $color1;
    }

    div:nth-of-type(2) {
      width: 100%;
      background-color: $color7 ;

      .VIcon {
        float: right;
        margin-right: 2rem;
        margin-top: 2rem;
        width: 60px;
        height: 60px;
        background-color: $color8;
        transition: all 0.5s;
        text-align: center;
      }
    }

    div:nth-of-type(2):hover .VIcon {
      transform: rotate(180deg);
      transform-origin: 30px 30px;
    }
  }
}

</style>